import Component from '../React/component';
import { diff, diffNode } from './diff';

export const createComponent = (comp, props) => {
  let inst;
  //  Âà§Êñ≠ÊòØÁ±ªÁªÑ‰ª∂ËøòÊòØÂáΩÊï∞ÁªÑ‰ª∂
  if (comp.prototype && comp.prototype.render) {
    //Á±ªÁªÑ‰ª∂
    inst = new comp(props);
  } else {
    //Â¶ÇÊûúÊòØÂáΩÊï∞,Â∞ÜÂáΩÊï∞ÁªÑ‰ª∂Êâ©Â±ïÊàêÁ±ªÁªÑ‰ª∂,Êñπ‰æøÂêéÈù¢Áªü‰∏ÄÂ§ÑÁêÜ
    inst = new Component(props);

    inst.constructor = comp; //Â∞ÜÊûÑÈÄ†ÁöÑÂØπË±°constructionÁöÑÂ±ûÊÄßÊåáÂêë‰º†ËøõÊù•ÁöÑÂáΩÊï∞
    inst.render = () => inst.constructor(props);
  }
  return inst;
};
export const setComponentProps = (comp, props) => {
  //ËÆæÁΩÆÂ±ûÊÄß
  comp.props = props;
  if (!comp.base) {
    comp.componentWillMount && comp.componentWillMount();
  } else if (comp.componentWillReceiveProps) {
    comp.componentWillReceiveProps();
  }
  //Ê∏≤ÊüìËäÇÁÇπ
  renderComponent(comp);
};
export const unmountComponent = (dom) => {
  // console.log('ÁßªÈô§ÁªÑ‰ª∂');
  dom && dom.parentNode && dom.parentNode.removeNode(dom);
};
export const renderComponent = (comp) => {
  let base;
  const renderer = comp.render(); //ËøîÂõûjsxÂØπË±°
  // base = _render(rendered); //ËøîÂõûËäÇÁÇπÂØπË±°
  base = diffNode(comp.base, renderer);
  if (comp.base) {
    comp.componentWillUpdate && comp.componentWillUpdate();
    comp.componentDidUpdate && comp.componentDidUpdate();
  } else if (comp.componentDidMount) {
    comp.componentDidMount();
  }
  // // Â¶ÇÊûúË∞ÉÁî®‰∫ÜsetState,ËäÇÁÇπÊõøÊç¢
  // if (comp.base && comp.base.parentNode) {
  //   comp.base.parentNode.replaceChild(base, comp.base);
  // }
  comp.base = base;
};
