const render = (node, container, dom) => {
  // return container.appendChild && container.appendChild(_render(node));
  return diff(dom, node, container);
};
export const setAttributeFunc = (dom, key, value) => {
  // Âà§Êñ≠keyÁöÑÁ±ªÂûã
  // Â¶ÇÊûúkeyÊòØÁ±ªÂêçÁõ¥Êé•ËΩ¨Êç¢Êàêclass
  if (key === 'className') {
    key = 'class';
  }
  // 1ÊòØ‰∫ã‰ª∂ Â¶ÇonClick,onBlur...
  if (/on\w+/.test(key)) {
    key = key.toLowerCase();
    dom[key] = value || '';
  } else if (key === 'style') {
    // 2ÊòØÊ†∑Âºè style
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '';
    } else if (typeof value === 'object') {
      //Ê†∑ÂºèÊòØÂØπË±°
      for (const k in value) {
        //Âæ™ÁéØÊ†∑Âºè
        if (typeof k === 'number') {
          dom.style[k] = value[k] + 'px';
        } else {
          dom.style[k] = value[k];
        }
      }
    }
  } else {
    // 3ÂÖ∂‰ªñÂ±ûÊÄß
    if (key in dom) {
      dom[key] = value || '';
    }
    if (value) {
      dom.setAttribute(key, value);
    } else {
      dom.removeAttribute(key);
    }
  }
};

const ReactDOM = {
  render,
};
export default ReactDOM;
