/*
dom:ÁúüÂÆûDOM
vNode:ËôöÊãüDOM
container: ÂÆπÂô®
return : Êõ¥Êñ∞ÂêéÁöÑDOM
*/
import { createComponent, setComponentProps, setAttributeFunc } from './index';
export const diff = (dom, vNode, container) => {
  let ret = diffNode(dom, vNode); //ÊØîËæÉËäÇÁÇπ
  container && container.appendChild(ret);
  return ret;
};
export const diffNode = (dom, vNode) => {
  let out = dom; //Âà§Êñ≠ËäÇÁÇπÁ±ªÂûã
  // Âà§Êñ≠ËäÇÁÇπÁ±ªÂûã
  if (
    vNode === undefined ||
    vNode === null ||
    vNode === '' ||
    typeof vNode === 'boolean'
  ) {
    return;
  }
  if (typeof vNode === 'number') {
    vNode = String(vNode);
  }
  //Â≠óÁ¨¶‰∏≤
  if (typeof vNode === 'string') {
    //Â¶ÇÊûúÂΩìÂâçËäÇÁÇπÊòØÊñáÊú¨ËäÇÁÇπ,Áõ¥Êé•Êõ¥Êñ∞ÂÜÖÂÆπ
    if (dom && dom.nodeType === 3) {
      dom.textContent !== vNode && (dom.textContent = vNode);
    } else {
      //Â¶ÇÊûúdom‰∏çÊòØÊñáÊú¨ËäÇÁÇπ,ÂàôÂàõÂª∫‰∏Ä‰∏™ÊñáÊú¨ÁÇπdom,Âπ∂ÁßªÈô§ÂéüÊù•ÁöÑdom
      out = document.createTextNode(vNode);
      if (dom && dom.parentNode) {
        dom.parentNode.replaceNode(out, dom);
      }
    }
    return out;
  }
  //Â¶ÇÊûúÊòØ‰∏Ä‰∏™ÁªÑ‰ª∂
  if (typeof vNode.tag === 'function') {
    return diffComponent(dom, vNode);
  }
  // Â¶ÇÊûúÊòØÈùûÊñáÊú¨DOMËäÇÁÇπ
  // ÁúüÂÆûDOM‰∏çÂ≠òÂú®
  if (!dom) {
    out = document.createElement(vNode.tag);
  }
  // Â¶ÇÊûúÁúüÂÆûDOMÂ≠òÂú®,ÈúÄË¶ÅÂØπÊØîÂ±ûÊÄßÂíåÂØπÊØîÂ≠êËäÇÁÇπ
  diffAttributes(out, vNode);
  if (
    (vNode.children && vNode.children.length > 0) ||
    (out.childNodes && out.childNodes.length > 0)
  ) {
    diffChildren(out, vNode.children);
  }

  return out;
};
const diffComponent = (dom, vNode) => {
  let comp = dom;
  //Â¶ÇÊûúÁªÑ‰ª∂Ê≤°ÂèòÂåñ,ÂàôÈáçÊñ∞ËÆæÁΩÆprops
  if (comp && comp.constructor === vNode.tag) {
    //ÈáçÊñ∞ËÆæÁΩÆprops
    setComponentProps(comp, vNode.attr);
    //ËµãÂÄº
    dom = comp.base;
  } else {
    // Â¶ÇÊûúÁªÑ‰ª∂Á±ªÂûãÂèòÂåñ,ÂàôÁßªÈô§ÊéâÂéüÊù•ÁªÑ‰ª∂,Âπ∂Ê∏≤ÊüìÊñ∞ÁªÑ‰ª∂
    // ÁßªÈô§
    if (comp) {
      unmountComponent(comp);
      comp = null;
    }
    //Ê†∏ÂøÉ‰ª£Á†Å
    // 1.ÂàõÂª∫Êñ∞ÁªÑ‰ª∂
    comp = createComponent(vNode.tag, vNode.attr);
    // 2.ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
    setComponentProps(comp, vNode.attr);
    // ÊåÇËΩΩÂΩìÂâçÊñ∞ÁöÑÁªÑ‰ª∂
    dom = comp.base;
  }
  return dom;
};
const diffAttributes = (dom, vNode) => {
  // ‰øùÂ≠ò‰πãÂâçÁöÑÁúüÂÆûDOMÁöÑÊâÄÊúâÂ±ûÊÄß
  const oldAttrs = {};
  const newAttrs = vNode.attr; //ËôöÊãüDOMÁöÑÂ±ûÊÄß (‰πüÊòØÊúÄÊñ∞ÁöÑÂ±ûÊÄß)
  // Ëé∑ÂèñÁúüÂÆûDOMÂ±ûÊÄß
  const domAttrs = dom.attributes;
  // const domAttrs =  document.querySelector('#root').attributes;
  [...domAttrs].forEach((item) => {
    oldAttrs[item.name] = item.value;
  });
  // ÊØîËæÉ
  // Â¶ÇÊûúÂéüÊù•ÁöÑÂ±ûÊÄß‰∏çÂú®Êñ∞ÁöÑÂ±ûÊÄßÂΩì‰∏≠,ÂàôÂ∞ÜÂÖ∂ÁßªÈô§Êéâ  (Â±ûÊÄßÂÄºÁõ¥Êé•ËÆæÁΩÆundefined)
  for (let key in oldAttrs) {
    if (!(key in newAttrs)) {
      // ÁßªÈô§
      setAttributeFunc(dom, key, undefined);
    }
  }
  // Êõ¥Êñ∞Êñ∞ÁöÑÂ±ûÊÄßÂÄº
  for (let key in newAttrs) {
    if (oldAttrs[key] !== newAttrs[key]) {
      // console.log(dom, newAttrs[key], key);
      // Âè™Êõ¥ÂÄº‰∏çÁõ∏Á≠âÁöÑÂ±ûÊÄß
      setAttributeFunc(dom, key, newAttrs[key]);
    }
  }
};
const diffChildren = (dom, vChildren) => {
  const domChildren = dom.childNodes;
  const children = [];
  const keyed = {};
  // Â∞ÜÊúâkeyÁöÑËäÇÁÇπ(Áî®ÂØπË±°‰øùÂ≠ò)ÂíåÊ≤°ÊúâkeyÁöÑËäÇÁÇπ(Áî®Êï∞ÁªÑ‰øùÂ≠ò)ÂàÜÂºÄ
  if (domChildren.length > 0) {
    [...domChildren].forEach((item) => {
      // Ëé∑Âèñkey
      const key = item.key;
      if (key) {
        // Â¶ÇÊûúkeyÂ≠òÂú®,‰øùÂ≠òÂà∞ÂØπË±°‰∏≠
        keyed[key] = item;
      } else {
        // Â¶ÇÊûúkey‰∏çÂ≠òÂú®,‰øùÂ≠òÂà∞Êï∞ÁªÑ‰∏≠
        children.push(item);
      }
    });
  }
  if (vChildren && vChildren.length > 0) {
    let min = 0;
    let childrenLen = children.length; //2
    [...vChildren].forEach((vChild, i) => {
      // Ëé∑ÂèñËôöÊãüDOM‰∏≠ÊâÄÊúâÁöÑkey
      const key = vChild.key;
      let child;
      if (key) {
        // Â¶ÇÊûúÊúâkey,ÊâæÂà∞ÂØπÂ∫îkeyÂÄºÁöÑËäÇÁÇπ
        if (keyed[key]) {
          child = keyed[key];
          keyed[key] = undefined;
        }
      } else if (childrenLen > min) {
        // alert(1);
        // Â¶ÇÊûúÊ≤°Êúâkey,Âàô‰ºòÂÖàÊâæÁ±ªÂûãÁõ∏ÂêåÁöÑËäÇÁÇπ
        for (let j = min; j < childrenLen; j++) {
          let c = children[j];
          if (c) {
            child = c;
            children[j] = undefined;
            if (j === childrenLen - 1) childrenLen--;
            if (j === min) min++;
            break;
          }
        }
      }
      // ÂØπÊØî
      child = diffNode(child, vChild);
      // Êõ¥Êñ∞DOM
      const f = domChildren[i];
      if (child && child !== dom && child !== f) {
        // Â¶ÇÊûúÊõ¥Êñ∞ÂâçÁöÑÂØπÂ∫î‰ΩçÁΩÆ‰∏∫Á©∫ÔºåËØ¥ÊòéÊ≠§ËäÇÁÇπÊòØÊñ∞Â¢ûÁöÑ
        if (!f) {
          dom.appendChild(child);
          // Â¶ÇÊûúÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÂíåÊõ¥Êñ∞ÂâçÂØπÂ∫î‰ΩçÁΩÆÁöÑ‰∏ã‰∏Ä‰∏™ËäÇÁÇπ‰∏ÄÊ†∑ÔºåËØ¥ÊòéÂΩìÂâç‰ΩçÁΩÆÁöÑËäÇÁÇπË¢´ÁßªÈô§‰∫Ü
        } else if (child === f.nextSibling) {
          removeNode(f);
          // Â∞ÜÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÁßªÂä®Âà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ
        } else {
          // Ê≥®ÊÑèinsertBeforeÁöÑÁî®Ê≥ïÔºåÁ¨¨‰∏Ä‰∏™ÂèÇÊï∞ÊòØË¶ÅÊèíÂÖ•ÁöÑËäÇÁÇπÔºåÁ¨¨‰∫å‰∏™ÂèÇÊï∞ÊòØÂ∑≤Â≠òÂú®ÁöÑËäÇÁÇπ
          dom.insertBefore(child, f);
        }
      }
    });
  }
};
