import { renderComponent } from '../React-dom/index';
const setStateQueue = []; //‰øÆÊîπÁöÑÈòüÂàó
const renderQueue = []; //Ê∏≤ÊüìÁöÑÁªÑ‰ª∂
//ÂàõÂª∫ÈòüÂàó
//ÈòüÂàó :ÂÖàËøõÂÖàÂá∫
const defer = (fn) => {
  return Promise.resolve().then(fn);
};
// ÂÆö‰πâ‰∏Ä‰∏™flushÊñπÊ≥ï,ÂÆÉÁöÑ‰ΩúÁî®Â∞±ÊòØÊ∏ÖÁ©∫ÈòüÂàó
const flush = () => {
  let item, comPonent;
  //ÈÅçÂéÜstate
  while ((item = setStateQueue.shift())) {
    const { stateChange, component } = item;
    // Â¶ÇÊûúÊ≤°ÊúâprevState,ÂàôÂΩìÂâçÁöÑstate‰Ωú‰∏∫ÂàùÂßãÁöÑprevState
    if (!component.prevState) {
      component.prevState = Object.assign({}, component.state);
    }
    // Â¶ÇÊûústateChangeÊòØ‰∏Ä‰∏™ÊñπÊ≥ï,‰πüÂ∞±ÊòØsetStateÁöÑÁ¨¨‰∏ÄÁßçÂΩ¢Âºè
    if (typeof stateChange === 'function') {
      Object.assign(
        component.state,
        stateChange(component.prevState, component.props)
      );
    } else {
      // Â¶ÇÊûústateChangeÊòØ‰∏Ä‰∏™ÂØπË±°,ÂàôÁõ¥Êé•ÂêàÂπ∂Âà∞setState‰∏≠
      Object.assign(component.state, stateChange);
    }
    component.prevState = component.state;
  }
  // ÈÅçÂéÜÁªÑ‰ª∂
  while ((comPonent = renderQueue.shift())) {
    renderComponent(comPonent);
  }
};
export const enqueueSetState = (stateChange, component) => {
  console.log('component', component);
  // Â¶ÇÊûúsetStateQueueÁöÑÈïøÂ∫¶ÊòØ0,‰πüÂ∞±ÊòØÂú®‰∏äÊ¨°flushÊâßË°å‰πãÂêéÁ¨¨‰∏ÄÊ¨°ÂæÄÈòüÂàóÈáåÊ∑ªÂä†
  if (setStateQueue.length === 0) {
    defer(flush);
  }
  //ÂÖ•Èòü ËÆ©ÊâÄÊúâËÆæÁΩÆÁöÑÁä∂ÊÄÅ ÈÉΩÊîæÂÖ•Âà∞ÈòüÂàó‰∏≠
  setStateQueue.push({
    stateChange,
    component,
  });

  // Â¶ÇÊûúrenderQueueÈáåÊ≤°ÊúâÂΩìÂâçÁªÑ‰ª∂,ÂàôÊ∑ªÂä†Âà∞ÈòüÂàó‰∏≠
  let r = renderQueue.some((item) => {
    return item === component;
  });
  if (!r) {
    renderQueue.push(component);
  }
};
